import Basemap from '@arcgis/core/Basemap';
import { GeometryUnion } from '@arcgis/core/unionTypes.js';
import esriId from '@arcgis/core/identity/IdentityManager';
import FeatureLayer from '@arcgis/core/layers/FeatureLayer';
import ImageryLayer from '@arcgis/core/layers/ImageryLayer';
import MapImageLayer from '@arcgis/core/layers/MapImageLayer';
import Layer from '@arcgis/core/layers/Layer';
import TileLayer from '@arcgis/core/layers/TileLayer';
import VectorTileLayer from '@arcgis/core/layers/VectorTileLayer';
import WMTSLayer from '@arcgis/core/layers/WMTSLayer';
import SwissTileLayer from './SwissTileLayer';
import esriRequest from '@arcgis/core/request';
import * as query from '@arcgis/core/rest/query';
import { BasemapItem, ConfigItem, LayerQueryItem } from '../types/ConfigItem';

export default class LayerUtils {
    private serviceDescription = null;
    private config: ConfigItem;
    private serviceUrl: string;

    constructor(config: ConfigItem) {
        this.config = config;
        this.serviceUrl = config.vectorServiceUrl;
        const token = config.vectorServiceToken;
        if (token) {
            esriId.registerToken({
                token,
                server: `${this.serviceUrl.split('/rest/services')[0]}/rest/services`,
            });
        }
    }

    getFeatureLayers(layers: string[]): Promise<FeatureLayer[]> {
        return new Promise((resolve) => {
            this.getServiceDescription().then((r) => {
                const featureLayers: FeatureLayer[] = [];
                r.layers.forEach((l) => {
                    if (layers.includes(l.name)) {
                        const featureLayer = new FeatureLayer({
                            url: `${this.serviceUrl}/${l.id}`,
                            title: l.name,
                        });
                        featureLayer.on('layerview-create', (e) => {
                            const fLayer = e.layerView.layer as FeatureLayer;
                            fLayer.popupTemplate = fLayer.createPopupTemplate();
                            // requte si le champ pricipal est une expression Arcade
                            /*esriRequest(url, {
                                query: {
                                    f: 'json',
                                },
                                responseType: 'json',
                            }).then((r2) => {
                                // r2.displayFieldExpressionInfo contient "$feature.VOIE_TXT + \" \" + $feature.NO_ENTREE + \", \" + $feature.CODEPOST_4 + \" \" + $feature.LOCALITE;"
                                // il faudrait convertir cela dans la chane ci-dessous
                                //fLayer.popupTemplate.title = '{VOIE_TXT} {NO_ENTREE}, {CODEPOST_4} {LOCALITE};';
                            });*/
                        });
                        featureLayers.push(featureLayer);
                    }
                });
                resolve(featureLayers);
            });
        });
    }

    async getMapImageLayers(layers: string[]): Promise<MapImageLayer[]> {
        const mapImageLayers: MapImageLayer[] = [];
        const r = await this.getServiceDescription();
        r.layers.forEach((l) => {
            if (layers.includes(l.name)) {
                const layer = new MapImageLayer({
                    title: l.name,
                    url: this.serviceUrl,
                    listMode: 'hide-children',
                    sublayers: [{
                        id: l.id,
                        visible: true,
                    }],
                });
                const sublayer = layer.sublayers!.at(0);
                sublayer!.load().then(() => {
                    sublayer!.popupEnabled = true;
                    sublayer!.popupTemplate = sublayer!.createPopupTemplate();
                    sublayer!.popupTemplate!.title = `${l.name}: {${sublayer!.sourceJSON.displayField}}`;
                    
                });
                mapImageLayers.push(layer);
            }
        });
        return mapImageLayers;
    }

    queryLayer(layerQuery: LayerQueryItem, ids: string[]): Promise<GeometryUnion[]> {
        return new Promise((resolve) => {
            this.getServiceDescription().then((r) => {
                const layerInfo = r.layers.filter((l) => l.name === layerQuery.layer)[0];
                if (!layerInfo) {
                    console.warn(`Invalid layer name in config file: ${layerQuery.layer}`);
                    resolve([]);
                    return;
                }
                const field = layerInfo.fields.filter((f) => f.name === layerQuery.field)[0];
                if (!field) {
                    console.warn(`Invalid field name in config file: ${layerQuery.field}`);
                    resolve([]);
                    return;
                }
                let where: string;
                if (this.isNumericField(field.type)) {
                    where = `${layerQuery.field} in (${ids.join(',')})`;
                } else {
                    where = `${layerQuery.field} in ('${ids.join('\',\'')}')`;
                }
                query.executeQueryJSON(`${this.serviceUrl}/${layerInfo.id}`, {
                    where,
                    returnGeometry: true,
                }).then((r2) => {
                    resolve(r2.features.map((f) => f.geometry!));
                });
            });
        });
    }

    getBasemaps(basemapItems: BasemapItem[]): Basemap[] {
        const result: Basemap[] = [];
        basemapItems.forEach((item) => {
            const layer = this.getLayer(item);
            if (layer) {
                result.push(new Basemap({
                    baseLayers: [layer],
                    title: item.alias,
                    thumbnailUrl: item.thumbnailUrl,
                }));
            }
        });
        return result;
    }

    private getServiceDescription(): Promise<any> {
        return new Promise((resolve) => {
            if (this.serviceDescription === null) {
                esriRequest(`${this.serviceUrl}/layers`, {
                    query: {
                        f: 'json',
                    },
                    responseType: 'json',
                }).then((r) => {
                    this.serviceDescription = r.data;
                    resolve(this.serviceDescription);
                });
            } else {
                resolve(this.serviceDescription);
            }
        });
    }

    private getLayer(params: BasemapItem): Layer | null {
        switch (params.type) {
            case 'tile':
                return new SwissTileLayer({
                    title: params.alias,
                    urlTemplate: params.urlTemplate,
                    scales: this.config.scales,
                });
            case 'wmts':
                return new WMTSLayer({
                    url: params.url,
                    activeLayer: {
                        id: params.layerId,
                    },
                    copyright: params.copyright,
                });
            case 'mapservice':
                return new TileLayer({
                    url: params.url,
                    copyright: params.copyright,
                });
            case 'imageservice':
                return new ImageryLayer({
                    url: params.url,
                    copyright: params.copyright,
                });
            case 'vectortile':
                return new VectorTileLayer({
                    url: params.url,
                });
            default:
                console.warn(`Unsupported basemap type: ${params.type}`);
                return null;
        }
    }

    private isNumericField(type: string): boolean {
        if (type === 'esriFieldTypeSmallInteger' ||
            type === 'esriFieldTypeInteger' ||
            type === 'esriFieldTypeSingle' ||
            type === 'esriFieldTypeDouble') {
            return true;
        }
        return false;
    }

}
