import Basemap from '@arcgis/core/Basemap';
import MapView from '@arcgis/core/views/MapView';
import LocalBasemapsSource from '@arcgis/core/widgets/BasemapGallery/support/LocalBasemapsSource';
import CoordinateConversion from '@arcgis/core/widgets/CoordinateConversion';
import Conversion from '@arcgis/core/widgets/CoordinateConversion/support/Conversion';
import Format from '@arcgis/core/widgets/CoordinateConversion/support/Format';
import Expand from '@arcgis/core/widgets/Expand';
import LayerList from '@arcgis/core/widgets/LayerList';
import '@arcgis/map-components/components/arcgis-basemap-gallery';
import Overview from '../widgets/Overview';

export default class WidgetUtils {

    static addOverview(basemap: Basemap, view: MapView, scales: number[], factor: number, display: 'collapsed' | 'expanded'): void {
        const overview = new Overview({
            basemap,
            mainView: view,
            scales,
            factor,
        });
        const overviewExpand = new Expand({
            expandIcon: 'map',
            view,
            content: overview,
            expanded: display === 'expanded',
            mode: 'floating',
            autoCollapse: false,
            group: 'api',
        });
        overview.expand = overviewExpand;
        view.ui.add(overviewExpand, {
            position: 'bottom-right',
        });
    }

    static addLayerList(view: MapView, display: 'collapsed' | 'expanded'): void {
        const layerList = new LayerList({
            view,
        });
        const layerListExpand = new Expand({
            expandIcon: 'layers',
            view,
            content: layerList,
            expanded: display === 'expanded',
            mode: 'floating',
            autoCollapse: false,
            group: 'api',
        });
        view.ui.add(layerListExpand, {
            position: 'top-right',
        });
    }

    static addBasemapGallery(items: Basemap[], view: MapView): void {
        const basemapGallery = document.createElement('arcgis-basemap-gallery') as any;
        basemapGallery.source = new LocalBasemapsSource({
            basemaps: items
        });
        basemapGallery.view = view;
        const container = document.createElement('div');
        container.appendChild(basemapGallery);
        const basemapGalleryExpand = new Expand({
            expandIcon: 'basemap',
            view,
            content: container,
            mode: 'floating',
            autoCollapse: true,
            group: 'api',
        });
        view.ui.add(basemapGalleryExpand, {
            position: 'top-right',
        });
    }

    static addCoordinates(view: MapView, display: 'collapsed' | 'expanded'): void {
        const coords = new CoordinateConversion({
            view,
        });
        coords.visibleElements = {
            expandButton: false,
        };

        coords.when(() => {
            const handle = setInterval(() => {
                const div = document.getElementsByClassName('esri-coordinate-conversion')[0] as HTMLElement;
                if (div) {
                    clearInterval(handle);
                    div.style.width = '300px';
                }
            }, 50);
            
            // copie du format basemap pour pouvoir donner un autre nom
            const basemap = coords.formats.find((f) => f.name === 'basemap');
            const mn95 = new Format({
                name: 'mn95',
                coordinateSegments: basemap!.coordinateSegments,
                spatialReference: basemap!.spatialReference,
            });

            // ajout du format MN95 par dfaut
            coords.formats = coords.formats.filter((f) => f.name === 'dd');
            coords.formats.add(mn95, 0);
            coords.conversions.removeAll();
            const conversion = new Conversion({
                format: mn95,
            });
            coords.conversions.add(conversion);
        });

        const coordsExpand = new Expand({
            expandIcon: 'gps-on',
            view,
            content: coords,
            expanded: display === 'expanded',
            mode: 'floating',
            autoCollapse: false,
            group: 'api',
        });
        view.ui.add(coordsExpand, {
            position: 'bottom-right',
        });
    }

}
